@import "../../../styles/variables.scss";

.product-specyfication{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 2rem;
    margin-top: 5rem;
    background-color: white;
    border-radius: $border-radius;
    box-shadow: $basic-box-shadow;
    &__header{
        font-size: 2.2rem;
        font-weight: $font-weight;
    }
}